<template>
  <div id="DeliveryTimeMaintenance">
    <el-form :inline="true">
      <el-form-item :model="form" label="国家">
        <el-select v-model="form.country" @change="changeCountry">
          <el-option
            v-for="(item, i) in countrys"
            :key="i"
            :value="item"
            :label="item"
          ></el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="站点">
        <el-select v-model="form.station">
          <el-option
            v-for="(item, i) in stations"
            :key="i"
            :value="item"
            :label="item"
          ></el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="各个时效">
        <el-select v-model="form.time_interval">
          <el-option
            v-for="(item, i) in time_intervals"
            :key="i"
            :value="item"
            :label="item"
          ></el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="配送时间">
        <el-input v-model="form.time"></el-input>
        <el-select v-model="form.unit" placeholder="请选择时间单位">
          <el-option
            v-for="(item, i) in time_units"
            :key="i"
            :value="item"
            :label="item"
          ></el-option>
        </el-select>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="submit">
          提交
        </el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
export default {
  data() {
    return {
      form: {
        country: "",
        station: "",
        time_interval: "",
        time: "",
        unit: ""
      },
      countrys: [],
      stations: [],
      periods: [],
      time_units: ["天", "小时"],
      time_intervals: ["出库时效", "取货时效", "有配送信息时效", "送达时效"]
    };
  },
  methods: {
    submit() {
      // 发送请求
      let url = "";
      let fd = new FormData();

      for (let key in this.form) {
        if (!this.form[key].length) {
          alert("所有字段必选（必填）");
          fd = null;
          return;
        }
        fd.append(key, this.form[key]);
      }
      this.$axios.post(url, fd).then(res => {
        console.log(res);
      });
    },
    changeCountry() {
      let url = this._url2 + "server/personnel_management/get_sign_area/";
      this.$axios
        .get(url, {
          params: {
            country: this.form.country
          }
        })
        .then(res => {
          this.stations = res.data.data;
          this.form.station = this.stations[0] || "";
        });
    }
  },
  created() {
    let url = "server/personnel_management/get_sign_country/";
    this.$axios.get(this._url2 + url).then(res => {
      this.countrys = res.data.data;
      this.form.country = this.countrys[0] || "";
    });
  }
};
</script>

<style scoped>
#DeliveryTimeMaintenance {
  text-align: center;
  padding-top: 22px;
  padding-left: 22px;
  background: rgba(255, 255, 255, 0.9);
}
</style>
<style>
#DeliveryTimeMaintenance .el-input {
  width: 220px;
}
</style>



